@charset "utf-8";
.with-header {
  padding-top: 1.1rem;
}
.ui-head-bar {
  position: fixed;
  border-bottom: .01rem solid #c9c7c8;
  width: 100%;
  height: 1.1rem;
  top: 0;
  line-height: 1.1rem;
  display: -webkit-flex;
  display: -webkit-box;
  background: #fafafa;
  z-index: 99;
}

.ui-head-bar:empty {
  display: none
}

.ui-head-bar .arr,.ui-head-bar .subfield {
  display: inline-block;
  padding: 0 15px;
  height: 100%;
  color: #5e5e5e;
  font-size: 20px
}

.ui-head-bar .arr .subfield-btn,.ui-head-bar .subfield .subfield-btn {
  background: url("data:image/png;base64,") no-repeat;
  background-size: 100% auto;
  width: 20px;
  height: 15px;
  display: inline-block
}

.ui-head-bar .title {
  -webkit-flex: 1;
  -webkit-box-flex: 1;
  text-align: center;
  line-height: 1.1rem;
  font-size: .45rem;
  color: #5e5e5e;
  font-weight: 800;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.ui-head-bar .search_from {
  padding-top: 7px
}

.ui-head-bar .search {
  display: block;
  position: relative;
  border-radius: 1px;
  height: 30px;
  line-height: 30px;
  top: 7px;
  border: 1px solid #f3f3f3;
  background: #ececec url("data:image/png;base64,") 2px center no-repeat;
  background-size: 17px 15px;
  text-align: left;
  display: -webkit-flex;
  display: -webkit-box
}

.ui-head-bar .search .icon {
  position: absolute
}

.ui-head-bar .search-keywords {
  -webkit-flex: 1;
  -webkit-box-flex: 1;
  overflow: hidden;
  padding: 0 5px 0 20px
}

.ui-head-bar .search-keywords input[type=text] {
  vertical-align: top;
  line-height: 24px;
  border: 0;
  padding: 0;
  background: none;
  width: 100%;
  position: relative;
  top: 2px;
  font-size: 16px
}

.ui-head-bar .search-enter-btn {
  background: url("data:image/png;base64,") center no-repeat;
  background-size: 100% 100%;
  width: 25px;
  height: 25px;
  display: inline-block;
  margin: 0 5px
}

.ui-head-bar .h-icon {
  display: inline-block;
  width: 61px;
  height: 21px;
  background: url(/h5x6/assets/img/header-logo.png) no-repeat 0 0;
  background-size: 62px;
  vertical-align: middle
}

.ui-head-bar .badge {
  display: inline-block;
  width: 2rem;
  text-align: center;
  position: relative
}

.ui-head-bar .badge,.ui-head-bar .badge * {
  color: #727272!important
}

.ui-head-bar .search-text:before {
  color: #777!important;
  position: relative!important;
  top: 0!important;
  left: 0!important
}

.ui-head-bar .iconfont:before {
  position: relative;
  top: 0
}

.ui-head-bar .icon-uniE80C:before,.ui-head-bar .icon-uniE802:before,.ui-head-bar .icon-uniE805:before,.ui-head-bar .icon-uniE810:before {
  background-size: 100%;
  background-position: center;
  width: .55rem;
  height: .54rem;
  display: inline-block;
  content: " "!important;
  position: relative!important;
  left: 0!important;
  top: 5px
}

.ui-head-bar .badge.icon-uniE810:before,.ui-head-bar .icon-uniE810:before {
  background-image: url("data:image/jpg;base64,")
}

.ui-head-bar .badge.icon-uniE805:before,.ui-head-bar .icon-uniE805:before {
  background-image: url("data:image/png;base64,")
}

.ui-head-bar .badge.icon-uniE802:before,.ui-head-bar .icon-uniE802:before {
  background-image: url("data:image/png;base64,")
}

.ui-head-bar .badge.icon-uniE80C:before,.ui-head-bar .icon-uniE80C:before {
  background-image: url("data:image/png;base64,")
}

.ui-head-bar .icon-back {
  background: url("../../../../src/assets/images/login/return.jpeg") center no-repeat;
  background-size: 100% 100%;
  width: .5rem;
  height: .5rem;
  display: inline-block;
  margin-top: .25rem;
}



