
.form-wrap {
  padding: 0 0.3rem;
}
.form-wrap .form-group {
  height: 1.36rem;
  line-height: 1.36rem;
  padding: 0 0.6rem;
  border-bottom: 1px solid #d8d8d9;
  background: #fff;
  position: relative;
  display: -webkit-box;
}
.input {
  -webkit-box-flex: 1;
  border: none;
  font-size: 0.4rem;
  color: #444;
  height: 1rem;
  line-height: 1rem;
  border-radius: 0;
}
.confirm-btn {
  margin-top: 0.6rem;
  display: block;
  width: 100%;
  background: #FF5777;
  color: #fff;
  height: 1.2rem;
  line-height: 1.2rem;
  border-radius: 3px;
  border: none;
  font-size: 0.44rem;
  text-align: center;
}
.tip {
  display: block;
  color: #666;
  font-size: 0.36rem;
  margin-top: 0.36rem;
}
.msg-validate {
  text-align: left;
  width: 5.7rem;
  border-right: .01rem solid  #d8d8d9;
}
.msg-btn, .left-time {
  position: absolute;
  right: 0;
  top: .06rem;
  height: 1.22rem;
  text-align: center;
  font-size: 0.4rem;
  line-height: 1.22rem;
  background: #fff;
  width: 2rem;
}
.user-content {
  min-width: 320px;
}
.area-text {
  height: 1.2rem;
  position: absolute;
  font-size: 0.4rem;
  color: #999;
}
.area-value {
  position: absolute;
  top: 0;
  right: .6rem;
  height: 100%;
  text-align: right;
  color: #333;
}
.area-arrow {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: .6rem;
  background-image: url('http://s16.mogucdn.com/p1/151222/upload_ie4gmmzxge2gkzrzguzdambqgiyde_17x29.png');
  background-repeat: no-repeat;
  background-position: 100% 50%;
  background-size: 50%;
}
.plus {
  font-size: 0.72rem;
  margin-right: 0.1rem;
  line-height: 1.4rem;
}
.areacode {
  width: 1rem;
  border-right: 1px solid  #d8d8d9;
}
.phone-number {
  width: 5.5rem;
  margin-left: 0.4rem;
}
.form_check .check_notice {
  margin: 0.4rem auto;
  font-size: 0.4rem;
  position: relative;
}
.form_check .check_notice a {
  position: absolute;
  right: 0;
  color: #ee4566;
}
.form_check .check_notice span {
  color: #333333;
}
.form_check .check_img {
  margin-left: -0.22rem;
  -webkit-user-select: none;
}
.form_check .check_img .img_div {
  float: left;
  width: 2.12rem;
  height: 2.12rem;
  margin-left: 0.22rem;
  background-color: #fff;
}
.form_check .check_img .img_div span {
  background-repeat: no-repeat;
  display: block;
  width: 2rem;
  height: 2rem;
  margin: 0.06rem;
  background-size: 12rem auto;
}
.form_check .check_img .transition {
  -webkit-transition: all 0.5s ease-in-out;
}
.form_check .check_img .f0 {
  background-position: 0 0;
}
.form_check .check_img .f1 {
  background-position: -3rem 0;
}
.form_check .check_img .f2 {
  background-position: -6rem 0;
}
.form_check .check_img .f3 {
  background-position: -9rem 0;
}



